.input {
  height: 4em;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-weight: bold;
  font-size: 1.4em;
  background-color: #000000;
  color: #ffffff;
  padding: 0.5em;
  outline: 1px solid #888888;
  margin-top: 20px;
  box-shadow: 5px,5px, 10px #b6acac, -5px,-5px,10px #eeebeb;
}